import React from 'react';
import { ThemeProvider } from '@material-ui/styles';
import { createMuiTheme } from '@material-ui/core/styles';
import paleta from './Paleta';
import tipografia from './Tipografia';
import tipografiaresponsiva from './TipografiaResponsiva';
import puntos from './Breakpoints';

const space = [0, 4, 8, 16, 32, 64];
const tipograf = Object.assign({},tipografia,tipografiaresponsiva);

const theme = createMuiTheme({
  palette: paleta,
  typography: tipograf,
  breakpoints: puntos,
  spacing: space,
  overrides: {
    //MuiCssBaseline styles
    MuiCssBaseline: {
      '@global': {
        '@font-face':{
          fontFamily: tipograf.fontFamily,
        },
        
      },
    },
    //MuiCssBaseline styles

    //MuiButton styles
    MuiButton: {
      root: {
        margin: 1,
        fontFamily: tipograf.roboto,
        '&:hover':{
          fontFamily: tipograf.monserrat,
        },
      },
    },
    //MuiButton styles

  },
});

{/*
TextField
Button
AppBar
Menu
Container
Grid** Definir varios tipos de grid
ImageField
Typografy
MediaQuerys
Footer
Links

*/}


const Estilo = (props) => {
  return (
    <ThemeProvider theme={theme}>
      {props.children}
    </ThemeProvider>
  );
}

export default Estilo;